import React from 'react';
import PropTypes from 'prop-types';

import classNames from 'classnames';


const Day = ({ shortName, isPast, isToday, weekday, isActive, longName, ...props }) => {
	return (
		<div
			{ ...props }
			data-value={ longName }
			className={ classNames('rmd-calendar--day', 'rmd-calendar--day__wd-' + weekday, {
				'rmd-calendar--day__today': isToday,
				'rmd-calendar--day__past': isPast,
				'rmd-calendar--day__active': isActive,
			}) }
		>
			<div className="rmd-calendar--day-wave"></div>
			<span className="rmd-calendar--day-name">{ shortName }</span>
		</div>
	)
};


Day.propTypes = {
	shortName: PropTypes.number.isRequired,
	isPast: PropTypes.bool.isRequired,
	isToday: PropTypes.bool.isRequired,
	weekday: PropTypes.number.isRequired,

	isActive: PropTypes.bool,
};

export default Day;



