import React from 'react';

import setDay from 'date-fns/set_day';
import format from 'date-fns/format';

const WDs = () => {

	const tempDay = new Date();

	const weekNames = Array.apply(null, {length: 7}).map((row, i) => {
		return (
			<div
				key={ 'wd-name-' + i }
				className={ 'rmd-calendar--wd-name rmd-calendar--wd-name__' + i}
			>
				{ format(setDay(tempDay, i), 'dd') }
			</div>
		)
	});

	return (
		<div className={ 'rmd-calendar--wds' } >
			{ weekNames }
		</div>
	)
};

export default WDs;



