import React, { Component } from 'react';

import Month from './Month';

import classNames from 'classnames';

import isSameMonth from 'date-fns/is_same_month'


class MonthTransition extends Component {

	constructor(props){
		super(props);

		this.state = {
			newMonth: null,
			month: this.props.month,
			inTransition: false,
		};

		this._refs = {
			$newMonth: undefined,
			$monthBlock: undefined,
		};

		this.counter = {
			newMonth: 0,
		};

		this.handleNextMonthRef = this.handleNextMonthRef.bind(this);
	}

	componentWillReceiveProps(nextProps) {

		const month = this.state.newMonth || this.state.month;

		if (! isSameMonth(nextProps.month, month)) {

			if (this._refs.$newMonth) {
				const $newMonth = this._refs.$newMonth;

				this.counter.newMonth += 1;
				const counter = this.counter.newMonth;

				const onTransitionEnd = e => {
					$newMonth.removeEventListener('transitionend', onTransitionEnd);

					if (counter !== this.counter.newMonth) {
						return;
					}

					this.setState({
						inTransition: false,
						month: this.state.newMonth,
						newMonth: null
					});
				};

				$newMonth.addEventListener('transitionend', onTransitionEnd);
			}

			this.setState({
				newMonth: nextProps.month,
				month: month,
				inTransition: false,
			});

			requestAnimationFrame(() => {
				requestAnimationFrame(() => {

					this.setState({
						inTransition: (nextProps.month > month) ? 'next' : 'prev',
					});
				});
			});

		}
	}

	handleNextMonthRef(el) {
		this._refs.$newMonth = el;
	}

	render() {

		return (
			<div
				className={ classNames('rmd-calendar--content-months-block', this.props.class, this.props.className, {
					'rmd-calendar--content-months-block__in-transition-next': this.state.inTransition === 'next',
					'rmd-calendar--content-months-block__in-transition-prev': this.state.inTransition === 'prev',
					'rmd-calendar--content-months-block__reset': ! this.state.inTransition,
				}) }
			>
				<div
					className={ 'rmd-calendar--content-month' }
				>
					<Month
						{ ...this.props }
						month={ this.state.month }
					/>
				</div>
				<div
					className={ 'rmd-calendar--content-month' }
					ref={ this.handleNextMonthRef }
				>
					{ this.state.newMonth ?
						<Month
							{ ...this.props }
							month={ this.state.newMonth }
						/>
						: null
					}
				</div>
			</div>
		)
	}
}

export default MonthTransition;



