import React, { Component } from 'react';
import PropTypes from 'prop-types';

import IconButton from '../Button/IconButton';
import Slide from '../Transition/Slide';

import format from 'date-fns/format';
import isSameMonth from 'date-fns/is_same_month';


class Control extends Component {
	constructor(props) {
		super(props);

		this.state = {
			prevMonth: null,
			activeMonth: this.props.month,
		}
	}

	componentWillReceiveProps(nextProps) {

		const activeMonth = this.state.activeMonth;

		if (! isSameMonth(nextProps.month, activeMonth)) {
			this.setState({
				activeMonth: nextProps.month,
				prevMonth: this.state.activeMonth,
			});
		}
	}

	render() {

		return (
			<div className={ 'rmd-calendar--control' }>
				<IconButton
					onClick={ this.props.onPrevMonth }
					className={ 'rmd-calendar--control-btn rmd-calendar--control-btn__prev' }
				>
					<svg className="rmd-calendar--control-btn-icon" viewBox="0 0 24 24"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path></svg>
				</IconButton>
				<div
					className={ 'rmd-calendar--control-month-name-block' }
				>
					<Slide
						activeKey={format(this.state.activeMonth, 'YYYY-MM')}
						animation={ this.state.activeMonth > this.state.prevMonth ? 'right-to-left' : 'left-to-right' }
					>
						<div
							data-slot="active"
							className={ 'rmd-calendar--control-month-name' }
						>
							{ format(this.state.activeMonth, 'MMMM YYYY') }
						</div>

						<div
							data-slot="prev"
							className={ 'rmd-calendar--control-month-name' }
							ref={ this.handleNextMonthRef }
						>
							{ this.state.prevMonth ?
								format(this.state.prevMonth, 'MMMM YYYY')
								: null
							}
						</div>
					</Slide>
				</div>
				<IconButton
					onClick={ this.props.onNextMonth }
					className={ 'rmd-calendar--control-btn rmd-calendar--control-btn__next' }
				>
					<svg className="rmd-calendar--control-btn-icon" viewBox="0 0 24 24"><path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"></path></svg>
				</IconButton>
			</div>
		)
	}
}


Control.propTypes = {
	month: PropTypes.instanceOf(Date).isRequired,
	onPrevMonth: PropTypes.func.isRequired,
	onNextMonth: PropTypes.func.isRequired,
};

export default Control;



